.bullet-list {
    width: 255px;

    @media (max-width: $breakpoint-xs) {
        width: 100%;
    }

    @media (max-width: $breakpoint-xxs) {
        max-width: 305px;
    }

    &__item {
        display: flex;
        align-items: center;
        text-transform: none;
        margin-bottom: 10px;

        &:before {
            content: '';
            align-self: baseline;
            min-width: 10px;
            height: 10px;
            margin-top: 6px;
            margin-right: 11px;
            background-color: $dark-shade-25;
            border-radius: 10px;

            @media (max-width: $breakpoint-xs) {
                margin-right: 6px;
            }
        }

        &:last-child {
            margin: 0;
        }
    }
}