.hotel-search {
    width: 320px;
    padding: 40px 29px 29px 29px;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
    border-radius: 4px;

    @media (max-width: $breakpoint-xxs) {
        padding: 44px 39px 29px 9px;
        width: 300px;

        .dropdown .text-field__icon {
            padding: 0;
        }
    }
    
    &__title {
        text-transform: none;
    }

    &__date-picker {
        display: flex;
        justify-content: space-between;
        margin-top: 15px;
    }
    
    &__dropdown {
        margin-top: 15px;
    }

    &__button {
        margin-top: 31px;
    }
}