.toggle {
    width: 264px;
    display: flex;

    &__label {
        position: relative;
        display: inline-block;
        width: 38px;
        height: 19px;
        border-radius: 20px;
        border: 1px solid $dark-shade-25;
        transition: all 0.3s;
        cursor: pointer;

        &::after {
            content: '';
            position: absolute;
            width: 12px;
            height: 12px;
            border-radius:50%;
            background-color: $dark-shade-25;
            top: 3.5px;
            left: 3px;
            transition: all 0.3s;
        }
    }

    &__input {
        display : none;
    }

    &__input:checked + &__label::after {
        left : 23px;
        background-color: $color-primary;
    }
    
    &__input:checked + &__label {
        border: 1px solid $color-primary;
    } 

    &__text {
        padding-left: 10px;
        line-height: 19px;
    }
}